import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute } from '@angular/router';
import { SaveService } from '@app/core/http/save.service';
import { LiveClass } from '@app/shared/models/live-class';
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-live-create',
  templateUrl: './live-create.component.html',
  styleUrls: ['./live-create.component.css']
})
export class LiveCreateComponent implements OnInit {
  public submitted: boolean = false;
  public sub: Subscription;
  public questionId:string;

  createForm: FormGroup = this.fb.group({
    name: ["", [Validators.required]],
    teacherName: ["", Validators.required],
    startDate: ["", Validators.required],
    endDate: ["", Validators.required],
    zoomUrl: ["", Validators.required],
  });

  constructor(private fb: FormBuilder, private _createService: SaveService,private _activeRoute:ActivatedRoute) { }

  ngOnInit() {
    this.questionId = this._activeRoute.snapshot.paramMap.get("liveId");
    let data = this._activeRoute.snapshot.data['data'];
    if (data)
      this.getDetail(data);

  }

  getDetail(data:LiveClass){
    this.createForm = this.fb.group({
      name: [data.name, [Validators.required]],
      teacherName: [data.teacherName, Validators.required],
      startDate: [data.startDate, Validators.required],
      endDate: [data.endDate, Validators.required],
      zoomUrl: [data.zoomUrl, Validators.required],
    });
  }


  goBack() {
    this._createService.goBackPrev();
  }

  onCreate() {
    console.log(this.createForm.value)
    this.submitted = true;
    if (this.createForm.invalid) {
      return;
    } else {
      this.sub = this._createService.onSaveLiveCreate(this.createForm.value, this.questionId).subscribe(
        () => {
          this.goBack();
        });
    }
  }

  get f() {
    return this.createForm.controls;
  }
}
