import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { LiveClassComponent } from './live-class/live-class.component';
import { LiveCreateComponent } from './live-create/live-create.component';
import { RouterModule, Routes } from '@angular/router';
import { BasicModule } from '@app/shared/basic-components/basic.module';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { LiveClassDetailResolver } from './live-detail.relover';


const routes: Routes = [
  {
    path: '',
    component: LiveClassComponent,
  },
  {
    path: 'create',
    component: LiveCreateComponent,
  },
  {
    path: 'create/:liveId',
    component: LiveCreateComponent,
    resolve: {
      data: LiveClassDetailResolver
    }
  },
]

@NgModule({
  declarations: [
    LiveClassComponent,
    LiveCreateComponent,
  ],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    BasicModule,
    FormsModule,
    ReactiveFormsModule

  ],
  providers:[
    LiveClassDetailResolver
  ]
})
export class LiveClassModule { }
